@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
*,html,body,header,main,footer,div,p,h1,h2,h3,h4,ul,li{box-sizing:border-box;padding:0;margin:0}a{text-decoration:none;color:inherit}html{font-family:"Roboto", sans-serif;font-size:10px}.footer{width:80vw;max-width:780px;margin:0 auto 30px;padding:10px 0;display:flex;justify-content:flex-end;border-top:1px solid #ececec}.footer>*{padding:0 10px;border-right:1px solid #ececec}.footer>*:last-child{border:none}.footer .footer__logout,.footer .footer__login{display:block;font-size:1.2rem;color:#ececec;cursor:pointer}.footer .footer__copyright{font-size:1.2rem;color:#ececec}

